import React, { Component } from "react";
import { ViewPager, Frame, Track, View, } from 'react-view-pager'
import ListOfPosts from "./ListOfPosts";
import OnBoarding1 from "./OnBoarding1";
import 'bootstrap/dist/css/bootstrap.min.css';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import MyPosts from "./MyPosts";
import GridTest from "./GridTest";
import RecentPosts from "./RecentPosts";
import OnBoarding2 from "./OnBoarding2";


export default class HomeScreen extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             value:0,

        }
    }
    
  render() {
    const firstPAge = () => {
        this.track.scrollTo(0)

      };
      const secondPage = ()=>{
          this.track.scrollTo(1);
          
      }
      const thirdPage=()=>{
        this.track.scrollTo(2)
      }

  const handleChange = (event, newValue) => {
    this.setState({value:newValue});
    console.log(newValue, 'yo yo yo')
  };

  
  
    return ( 
      <div className="ui container" style={{alignItems:"center", alignContent:"center",justifyContent:"center",}}>
        <ViewPager initialPage={0}>
          
          <Frame>
            <div
              style={{
                display: "grid",
                color: "white",
                height: "10vh",
                backgroundColor: "rgb(57, 55, 119)"
              }}
              className="homeHeader">
              <h3 style={{ margin: "auto" }}>HOME SCREEN</h3>
            </div>
            <div style={{width:"100%"}}>
            {/* <div class="ui pointing secondary menu">
    <a class="active item">Tab 1</a>
    <a class="item">Tab 2</a>
    <a class="item">Tab 3</a>
  </div>
  <div class="ui segment active tab">Tab 1 Content</div> */}
            <Paper  square>
  <Tabs
    value={this.state.value}
    indicatorColor="blue"
    textColor="primary"
    onChange={handleChange}
    aria-label="disabled tabs example"
    
  >
    <Tab label="RECENT" onClick={firstPAge}/>
    <div style={{width:"100%"}}></div>
    <Tab label="LOCATION" onClick={secondPage}/>
    <div style={{width:"100%"}}></div>
    <Tab label="MINE" onClick={thirdPage}/>
  </Tabs>
</Paper>
</div>

            <Track
              ref={c => (this.track = c)}
              viewsToShow={1}
              // infinite
              className="track"
              
            >
              <View key="1">
              <RecentPosts/>
              </View>
              <View key="2">
              <OnBoarding1/>

              </View>
              <View key="3">
              <OnBoarding2/>


              </View>
            </Track>
          </Frame>
        </ViewPager>
      </div>
    );
  }
}
