import React, { Component } from "react";
import '../semantic/dist/semantic.css'
import NavNumbers from './NavNumbers'
import { Grid, Image } from 'semantic-ui-react'
import lamp from '../pics/lamp.png'


export default class NewRequestFinal_01 extends Component {
  
  render() {
    
    return (       
      <div style={{alignItems:"center", alignContent:"center", display:"grid"}} className="container">
        <NavNumbers/>
        <div>
          <p>Let’s start by choosing a category that best describes your request. This helps other people to see your request quickly.</p>
        </div>
        <br/>
          <div class="ui equal width grid">
            <div class="row">
              <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto", width:"76pt"}} className="ui mini image" /><span style={{margin:"auto", fontSize:"14pt"}}>Emergency</span></div></div>
              <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto"}} className="ui mini image" /><span style={{margin:"auto"}}>Medical</span></div></div>
              <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto"}} className="ui mini image" /><span style={{margin:"auto"}}>Ride/Delivery</span></div></div>
            </div>
            <div class="row">
            <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto"}} className="ui mini image" /><span style={{margin:"auto", }}>Road assistant</span></div></div>
            <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto"}} className="ui mini image" /><span style={{margin:"auto"}}>Item needed</span></div></div>
            <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto"}} className="ui mini image" /><span style={{margin:"auto"}}>Give away item</span></div></div>
            </div>
            <div class="row">
            <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto"}} className="ui mini image" /><span style={{margin:"auto"}}>Lost and found</span></div></div>
            <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto"}} className="ui mini image" /><span style={{margin:"auto"}}>Social</span></div></div>
            <div class="column"><div style={{display:"grid"}} class="ui segment"><img src={lamp} style={{margin:"auto"}} className="ui mini image" /><span style={{margin:"auto"}}>General</span></div></div>
            </div>
          </div>
        
      </div>
    );
  }
}
