import React, { Component } from 'react'
import myPosts from './myposts.json'
import lamp from '../pics/lamp.png'
import location from '../pics/location.png'
import arrow from '../pics/arrow.png'
import clock from '../pics/clock.png'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Image, Item } from 'semantic-ui-react'

export default class MyPosts extends Component {
    componentDidMount(){
    
    
    }

    render() {


        return (
            <div className="container">
            {myPosts.map(post=>{
                return <div style={{}}>
                        
                        {/* grid for each post (without icons) */}
                        <div className="ui grid">
                            <div className="one wide column">
                                <img src={lamp} className="ui mini image" />
                            </div>
                            <div className="nine wide column">
                                <h4 style={{textAlign:"left",fontSize:"17pt"}}>{post.title}</h4>
                                <p style={{textAlign:"left",fontSize:"16pt",maxWidth:"100%"}}>{post.body}</p>
                                <div style={{}} className="row">
                                    <span className="col-1"></span>
                                    <img className="col-1" alt="adress" style={{width:"20px"}} src={location}></img><span className="col-1">adress</span>
                                    <span className="col-2"></span>
                                    <img className="col-1" alt="distance" style={{width:"20px"}} src={arrow}></img><span className="col-1">distance</span>
                                    <span className="col-2"></span>
                                    <img className="col-1" alt="time" style={{width:"20px"}} src={clock}></img><span className="col-1">time</span>
                                    <span className="col-1"></span>
                                </div>
                            </div>
                            <div className="six wide column"></div>
                            </div>
                            {/* icons: */}
                            
                            <hr/>
                </div>
            })}
                
            </div>
        )
    }
}
